<template>
  <div class="condition-item">
    <el-select
      :value="describe"
      placeholder=""
      filterable
      @change="value => change('describe', value)"
    >
      <el-option
        v-for="item in options.keyOptions"
        :key="item.value"
        :label="item.label"
        :value="item.value"
      />
    </el-select>

    <el-select
      :value="relation"
      placeholder=""
      @change="value => change('relation', value)"
    >
      <el-option
        v-for="item in relationOptions"
        :key="item.value"
        :label="item.label"
        :value="item.value"
      />
    </el-select>

    <template>
      <el-input
        v-if="valueInput.type === 0"
        type="text"
        style="width: 180px;"
        placeholder=""
      />

      <!--  -->
      <el-select
        v-else-if="valueInput.type === 1"
        :value="value"
        placeholder=""
        filterable
        @change="value => change('value', value)"
      >
        <el-option
          v-for="item in valueInput.options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        />
      </el-select>
      <!-- / -->

      <!--  -->
      <el-select
        v-if="valueInput.type === 2"
        :value="value.length ? JSON.parse(value) : []"
        placeholder=""
        filterable
        multiple
        style="width: 500px;"
        @change="value => change('value', JSON.stringify(value))"
      >
        <el-option
          v-for="item in valueInput.options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        />
      </el-select>
      <!-- / -->

      <!--  -->
      <el-input
        v-else-if="valueInput.type === 3"
        type="number"
        :value="value"
        style="width: 180px;"
        placeholder=""
        @change="value => change('value', value.toString())"
      />
      <!-- / -->

      <!--  -->
      <el-date-picker
        v-else-if="valueInput.type === 4"
        :value="value"
        type="date"
        placeholder=""
        value-format="yyyy-MM-dd"
        @input="value => change('value', value)"
      />
      <!-- / -->

      <!--  -->
      <el-date-picker
        v-else-if="valueInput.type === 5"
        :value="value.length ? JSON.parse(value) : []"
        type="daterange"
        value-format="yyyy-MM-dd"
        @input="value => change('value', JSON.stringify(value))"
      />
      <!-- / -->

    </template>

    <el-button
      type="danger"
      icon="el-icon-delete"
      style="float: right;"
      @click="deleteItem"
    />
  </div>
</template>

<script>
import { Button, Select, Option, Input, DatePicker } from 'element-ui'

export default {
  components: {
    [Button.name]: Button,
    [Select.name]: Select,
    [Input.name]: Input,
    [Option.name]: Option,
    [DatePicker.name]: DatePicker
  },
  props: {
    options: {
      type: Object,
      required: true
    },
    describe: {
      type: String,
      default: ''
    },
    relation: {
      type: String,
      default: ''
    },
    value: {
      type: String,
      default: ''
    }
  },
  computed: {
    relationOptions () {
      const describe = this.describe
      return describe !== '' ? this.options.relationOptions[describe] : []
    },
    valueInput () {
      const describe = this.describe
      return describe !== '' ? this.options.valueInputs[describe] : {
        type: 1,
        options: []
      }
    }
  },
  methods: {
    change (key, value) {
      if (key === 'describe') {
        this.$emit('change', { key: 'relation', value: '' })
        this.$emit('change', { key: 'value', value: '' })
      }
      this.$emit('change', { key, value })
    },
    deleteItem () {
      this.$emit('deleteItem')
    }
  }
}
</script>
